import { Answer } from '../types';

export const trail = (
  name: string,
  data?: { [key: string]: string | Answer }
) => {
  const payload =
    data &&
    Object.fromEntries(Object.entries(data).map(([k, v]) => [k, stringify(v)]));
  // @ts-ignore
  if (!window || !window.splitbee) {
    // eslint-disable-next-line no-console
    console.log('No splitbee');
  }
  // @ts-ignore
  // eslint-disable-next-line no-console
  console.log('track', name, payload);
  // eslint-disable-next-line
  //window.splitbee.track(name, data);
};

const stringify = (value: Answer | string): string => {
  if (typeof value === 'string') {
    return value;
  }
  if (!value) {
    return null;
  }
  if (value instanceof Array) {
    return value.map((v) => v.value).join(',');
  }
  return value.value;
};

